import { FC } from 'react';
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import { PrivateRoute, PublicRoute } from 'components';
import 'tailwindcss/tailwind.css';

const App: FC = () => (
  <>
    <Router>
      <Switch>
        <PublicRoute path="/login" component={<div>login</div>} />
        <PrivateRoute path="/new-business/:tab" component={<div>new-business</div>} />
      </Switch>
    </Router>
  </>
);

export default App;
