import { FC } from 'react';
import { Route } from 'react-router-dom';

interface Props {
  component: JSX.Element,
  path: string;
}

// If user isn't logged-in, redirect to "/login"
const PrivateRoute: FC<Props> = ({ component, ...props }) => (
  <Route
    {...props}
    render={() => component}
    exact
  />
);

export default PrivateRoute;
